.mango-widget {
	animation: boxshadow 2s ease-in-out infinite;
	bottom: 20px;
	position: fixed;
	right: 10px;
	z-index: 100;

	img {
		max-height: 75px;
		height: auto;
		width: 100%;

		@include m--mobile {
			max-height: 50px;
		}
	}
	@keyframes boxshadow {
		0% {
			filter: drop-shadow(0px 1px 2px black);
		}
		50% {
			filter: drop-shadow(0px 5px 5px black);
		}
		100% {
			filter: drop-shadow(0px 1px 2px black);
		}
	}
}
